'use client'

import { useEffect, useCallback } from 'react'
import { createClient } from '@/utils/supabase/client'
import { toast } from 'sonner'
import { RealtimePostgresChangesPayload } from '@supabase/supabase-js'

type Shipment = {
    id: string
    tracking_number: string | null
    status: string | null
    current_location: string | null
    [key: string]: any
}

type RealtimeOptions = {
    onInsert?: (shipment: Shipment) => void
    onUpdate?: (shipment: Shipment) => void
    onDelete?: (oldShipment: Shipment) => void
    showToasts?: boolean
    filter?: {
        column: string
        value: string
    }
}

/**
 * Hook for subscribing to real-time shipment updates via Supabase
 * @param options Configuration options for the subscription
 */
export function useRealtimeShipments(options: RealtimeOptions = {}) {
    const { onInsert, onUpdate, onDelete, showToasts = true, filter } = options
    const supabase = createClient()

    const handleChanges = useCallback((payload: RealtimePostgresChangesPayload<Shipment>) => {
        const { eventType, new: newRecord, old: oldRecord } = payload

        switch (eventType) {
            case 'INSERT':
                if (newRecord && onInsert) {
                    onInsert(newRecord as Shipment)
                    if (showToasts) {
                        toast.success('New shipment created', {
                            description: `Tracking: ${(newRecord as Shipment).tracking_number}`,
                        })
                    }
                }
                break

            case 'UPDATE':
                if (newRecord && onUpdate) {
                    onUpdate(newRecord as Shipment)
                    if (showToasts) {
                        const shipment = newRecord as Shipment
                        toast.info('Shipment updated', {
                            description: `${shipment.tracking_number} - ${shipment.status}`,
                        })
                    }
                }
                break

            case 'DELETE':
                if (oldRecord && onDelete) {
                    onDelete(oldRecord as Shipment)
                    if (showToasts) {
                        toast.warning('Shipment removed')
                    }
                }
                break
        }
    }, [onInsert, onUpdate, onDelete, showToasts])

    useEffect(() => {
        // Build the channel configuration
        let channelConfig: any = {
            event: '*',
            schema: 'public',
            table: 'shipments',
        }

        // Add filter if provided (e.g., for user-specific shipments)
        if (filter) {
            channelConfig.filter = `${filter.column}=eq.${filter.value}`
        }

        const channel = supabase
            .channel('shipments-realtime')
            .on(
                'postgres_changes',
                channelConfig,
                handleChanges
            )
            .subscribe((status) => {
                if (status === 'SUBSCRIBED') {
                    console.log('Realtime subscription active for shipments')
                }
            })

        return () => {
            supabase.removeChannel(channel)
        }
    }, [supabase, handleChanges, filter])
}

/**
 * Hook for subscribing to a specific shipment by tracking number
 */
export function useRealtimeShipment(trackingNumber: string, onUpdate: (shipment: Shipment) => void) {
    const supabase = createClient()

    useEffect(() => {
        if (!trackingNumber) return

        const channel = supabase
            .channel(`shipment-${trackingNumber}`)
            .on(
                'postgres_changes',
                {
                    event: 'UPDATE',
                    schema: 'public',
                    table: 'shipments',
                    filter: `tracking_number=eq.${trackingNumber}`,
                },
                (payload) => {
                    if (payload.new) {
                        onUpdate(payload.new as Shipment)
                        toast.info('Shipment status updated!', {
                            description: `Status: ${(payload.new as Shipment).status}`,
                        })
                    }
                }
            )
            .subscribe()

        return () => {
            supabase.removeChannel(channel)
        }
    }, [supabase, trackingNumber, onUpdate])
}
